import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the SampleLottiePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sample-lottie',
  templateUrl: 'sample-lottie.html',
})
export class SampleLottiePage {
  lottieConfig: any;
  anim: any;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.lottieConfig = {
      path: 'assets/file/tes-lottie.json',
      renderer: 'canvas',
      autoplay: true,
      loop: true
  };
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SampleLottiePage');
  }

  handleAnimation(anim: any) {
    console.log('anim', anim)
    this.anim = anim;
    this.anim.setSpeed(1);
}

}



